import { negToUint8, parseHexArray } from "@/common/index";
import { useStore } from "@/hooks/stores/global";
import { storeToRefs } from "pinia";

const reportParams = (value) => {
  value = value.replace(/[^0-9a-fA-F]/g, "");
  let data = parseHexArray(value);
  let reportId = data.getUint8(0);
  let reportData = new Uint8Array(data.buffer).slice(1);
  return {
    reportId,
    reportData,
  };
};
const getCheckSum = (strData, maxSize = 63) => {
  strData = strData.replace(/[^0-9a-fA-F]/g, "");

  let data = parseHexArray(strData);

  let pDataArray = new Uint8Array(data.buffer).slice(1);

  let sum = 0;
  for (let i = 0; i < maxSize; i++) {
    sum += pDataArray[i];
  }
  sum %= 256;
  // console.log("sum", sum);
  let value = parseInt("A1", 16) - sum;

  // console.log(value.toString(16))
  value = value >= 0 ? value : negToUint8(value);

  return value.toString(16).padStart(2, "0");
};

export const send_update = async (send_data, byte = "1d", byte0 = "9e") => {
 
  return new Promise((resolve, reject) => {
    const main = useStore();
    const { device, appRNG, appUpdateData, dfuData, dfuData1 } =
      storeToRefs(main);
    const byte1 = byte;
   
    enter_dfu(byte1, send_data);

 
    let timer2 = setInterval(() => {
      if (dfuData.value.length) {
        senddfu_hexinfo(byte1, send_data, dfuData.value);
        clearInterval(timer2);
      }
    });
  
    
    let timer3 = setInterval(() => {
      if (dfuData1.value.length) {
        send_bag3(byte1, send_data);
        console.log(2)
        clearInterval(timer3);
      }
    });
    
    let timer4 = setInterval(() => {
      if (appRNG.value.length) {
        send();
       
        clearInterval(timer4);
      }
    });

    
    let hex = "";
    for (let i = 15; i >= 12; i--) {
      hex += send_data[i].padStart(2, "0");
    }
    const maxByte = 73 + parseInt(hex, 16);
    const data = new Array(63).fill("00");
    data[1] = "-1";
    data[0] = byte0;
    let i = 74;
    let j = 4;
    let count = 0;
    // 发送bin文件数据包
    async function send() {
      for (let k = 4; k < 63; k++) {
        data[k] = "00";
      }
      for (j = 4; j < 63; j++) {
        data[j] = send_data[i++].toString(16).padStart(2, "0");
        if (i > maxByte) {
          j++;
          break;
        }
      }
      if (j !== 63) {
        data[3] = "93";
      } else {
        data[3] = "3b";
      }

      data[2] =
        data[1].toLowerCase() === "ff" ? parseInt(data[2], 16) + 1 : data[2];
      data[1] =
        data[1].toLowerCase() === "ff" ? "00" : parseInt(data[1], 16) + 1;
      data[2] = data[2].toString(16).padStart(2, "0");
      data[1] = data[1].toString(16).padStart(2, "0");

      const str = "00" + data.join("");
      let { reportId, reportData } = reportParams(str + getCheckSum(str));
      await device.value.sendReport(reportId, reportData);
    }
    // setTimeout(() => {
    //   send();
    // }, 400);

    let timer = setInterval(async () => {
      if (
        appUpdateData.value.length &&
        appUpdateData.value[2].toLowerCase() === data[1].toLowerCase() &&
        appUpdateData.value[3].toLowerCase() === data[2].toLowerCase()
      ) {
        count++;
        const percentage = Math.ceil(
          (count / Math.ceil((maxByte - 73) / 59)) * 100
        );
        main.getUpdatePercentage(percentage);
        send();
        if (i > maxByte) {
          clearInterval(timer);
          setTimeout(() => {
            send_end_data(
              appRNG.value,
              byte1,
              send_data[20].padStart(2, "0"),
              send_data[21].padStart(2, "0")
            );
          }, 100);
          setTimeout(() => {
            resolve();
          }, 200);
        }
      }
    });
  });
};

//进入dfu状态  1d 00 8b 01 (f0 07 00 00)hexPID     无线  1d 00 8b 01   d0 07
export const enter_dfu = async (byte1, bin_data) => {
  const main = useStore();
  const { device, mouseData } = storeToRefs(main);
  const data = new Array(63).fill("00");
  data[0] = byte1;
  data[2] = "8b";
  data[3] = "01";

  data[4] = bin_data[0].padStart(2, "0");
  data[5] = bin_data[1].padStart(2, "0");
  data[6] = bin_data[2].padStart(2, "0");
  data[7] = bin_data[3].padStart(2, "0");

  const value = "00" + data.join("");
  const { reportId, reportData } = reportParams(value + getCheckSum(value));
  console.log("value", value + getCheckSum(value));
  await device.value.sendReport(reportId, reportData);
};

