//1d 00 8d 06 00     24 00 00  00 74 00 00   91 a7 16 4e 无线
const send_bag3 = async (byte1, bin_data) => {
  const main = useStore();
  const { device } = storeToRefs(main);
  const data = new Array(63).fill("00");
  data[0] = byte1;
  data[2] = "8d";
  data[3] = "06";

  data[4] = bin_data[36].padStart(2, "0");
  data[5] = bin_data[37].padStart(2, "0");
  data[6] = bin_data[38].padStart(2, "0");
  data[7] = bin_data[39].padStart(2, "0");

  data[8] = bin_data[12].padStart(2, "0");
  data[9] = bin_data[13].padStart(2, "0");
  data[10] = bin_data[14].padStart(2, "0");
  data[11] = bin_data[15].padStart(2, "0");

  data[12] = bin_data[16].padStart(2, "0");
  data[13] = bin_data[17].padStart(2, "0");
  data[14] = bin_data[18].padStart(2, "0");
  data[15] = bin_data[19].padStart(2, "0");

  const value = "00" + data.join("");
  const { reportId, reportData } = reportParams(value + getCheckSum(value));
  await device.value.sendReport(reportId, reportData);
};

// bin文件数据包发送完,结尾的包
// byte2和byte3两个字节代表,升级包得版本号  byte1表示是dongle升级还是鼠标升级
const send_end_data = async (CheckData, byte1, byte2, byte3) => {
  const main = useStore();
  const { device } = storeToRefs(main);
  const data = new Array(63).fill("00");
  data[0] = byte1;
  data[2] = "88";
  data[3] = "07";

  const array = CheckData.slice(5, 10).map((item) => parseInt(item, 16));
  data[5] = ((array[0] + array[1]) ^ array[4]) & 255;
  data[6] = ((array[1] + array[2]) ^ array[3]) & 255;
  data[7] = ((array[4] + array[3]) ^ array[1]) & 255;
  data[8] = ((array[3] + array[4]) ^ array[0]) & 255;
  for (let i = 5; i <= 8; i++) {
    data[i] = data[i].toString(16).padStart(2, "0");
  }

  data[9] = byte2;
  data[10] = byte3;

  const value = "00" + data.join("");
  const { reportId, reportData } = reportParams(value + getCheckSum(value));
  await device.value.sendReport(reportId, reportData);
};

